<template>
  <div class="icons">
    <!--:options="swiperOption"-->
    <swiper  :options="swiperOption">
      <swiper-slide v-for="(page,index) of pages" :key="index"  >

        <div class="icon" v-for="item of page" :key="item.id">
          <div class="icon-img">
            <img class="icon-img-content" :src="item.imgUrl"
                 alt="景点门票" style="opacity: 1;">
          </div>
          <p class="icon-desc">{{item.desc}}</p>
        </div>
      </swiper-slide>
      <!-- Optional controls -->
      <div class="swiper-pagination"  slot="pagination">22</div>
    </swiper>
  </div>

</template>

<script>
  export default {
    name: "Icons",
    props:{
      list:Array
    },
    data() {
      return {
        swiperOption:{
          pagination:'.swiper-pagination', //小圆点
          loop:true,
          autoplay:false
        },
      }
    },
    computed: {
      pages (){
        const pages =[];
        this.list.forEach((item,index) =>{
          const page =Math.floor(index/8);
          if(!pages[page]) {
            pages[page]=[]
          }
          pages[page].push(item)
        });
        return pages
      },

    }
  }
</script>

<style lang="scss" scoped>
  @import '~@/assets/scss/varibles.scss';
  @import '~@/assets/scss/mixins.scss';

  .icons > > > .swiper-container {
    height: 0;
    padding-bottom: 50%;
  }

  .icons {
    margin-top:.1rem;
    .icon {
      position: relative;
      overflow: hidden;
      float: left;
      width: 25%;
      height: 0;
      padding-bottom: 25%;

      .icon-img {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: .44rem;
        box-sizing: border-box;
        padding: .1rem;
        .icon-img-content {
          display: block;
          margin: 0 auto;
          height: 100%;

        }
      }
      .icon-desc {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        height: .44rem;
        line-height: .44rem;
        text-align: center;
        color: $darkTextColor;
        @include ellipsis;
      }
    }
  }

</style>



